                                <span style={{ fontSize: '0.875rem', fontWeight: 500 }}>
                                    DSR {dsr.targetDSR}% ÌïúÎèÑÎ•º Ï¥àÍ≥ºÌï©ÎãàÎã§. ÎåÄÏ∂ú Í∏àÏï° ÎòêÎäî Í∏∞Í∞Ñ Ï°∞Ï†ïÏù¥ ÌïÑÏöîÌï©ÎãàÎã§.
                                </span>
